import React from "react";
import { FaShoppingCart } from "react-icons/fa";
import {
  Container,
  FormControl,
  Navbar,
  NavbarBrand,
  Nav,
  Dropdown,
  Badge
} from "react-bootstrap";
import { CartState } from "../context/Context";
import CartProduct from "./CartProduct";
import { Link } from "react-router-dom";
const Header = () => {
  const {
    state: { cart },
    dispatch
  } = CartState();

  return (
    <Navbar bg="dark" variant="dark" style={{ height: 80 }} className="header">
      <Container>
        <NavbarBrand>
          <Link to="/">Shopping Cart</Link>
        </NavbarBrand>
        <Navbar.Text className="search">
          <FormControl
            style={{ width: 300 }}
            placeholder="Search"
            className="m-auto"
          />
        </Navbar.Text>
        <Nav>
          <Dropdown align="end">
            <Dropdown.Toggle variant="success" id="dropdown-basic">
              <Link to="/cart">
                <FaShoppingCart fontSize="20px" style={{ marginRight: 10 }} />
              </Link>
              <Badge bg="success" className="cartQty">
                {cart.length}
              </Badge>
            </Dropdown.Toggle>

            <Dropdown.Menu style={{ width: "250px", padding: "10px" }}>
              {cart.length > 0 ? (
                <>
                  {cart.map((p) => (
                    <CartProduct key={p.id} prod={p} dispatch={dispatch} />
                  ))}
                </>
              ) : (
                <span>Cart is Empty</span>
              )}
            </Dropdown.Menu>
          </Dropdown>
        </Nav>
      </Container>
    </Navbar>
  );
};

export default Header;
