import React from "react";
import { Card, Button } from "react-bootstrap";
import { BsCartPlusFill, BsCartXFill } from "react-icons/bs";
import { CartState } from "../context/Context";
import Rating from "./Rating";
const SingleProduct = ({ prod }) => {
  const {
    state: { cart },
    dispatch
  } = CartState();

  console.log(cart);

  return (
    <div>
      <Card style={{ width: "18rem", margin: "10px" }}>
        <Card.Img variant="top" src={prod.image} />
        <Card.Body>
          <div className="cardBody">
            <Card.Title className="cardText cardTitle">
              {prod.name}
              <span className="cardRating">
                <Rating rating={prod.ratings} />
              </span>
            </Card.Title>

            <Card.Text className="cardText">{prod.price}</Card.Text>
            <Card.Text className="cardText ">
              {prod.fastDelivery && "Fast Delivery"}
            </Card.Text>
          </div>
          {cart.some((p) => p.id === prod.id) ? (
            <Button
              variant="danger"
              className="cardbtn"
              onClick={() => {
                dispatch({
                  type: "REMOVE_FROM_CART",
                  payload: prod
                });
              }}
            >
              <BsCartXFill />
            </Button>
          ) : (
            <Button
              variant="primary"
              disabled={!prod.inStock}
              className="cardbtn"
              onClick={() => {
                dispatch({
                  type: "ADD_TO_CART",
                  payload: prod
                });
              }}
            >
              <BsCartPlusFill />
            </Button>
          )}
        </Card.Body>
      </Card>
    </div>
  );
};

export default SingleProduct;
