import React, { useState } from "react";
import { IoOptionsSharp } from "react-icons/io5";
import { Button, Form } from "react-bootstrap";
import Rating from "./Rating";
const Filter = () => {
  const [rating, setRating] = useState(0);
  const [open, setOpen] = useState(false);
  return (
    <div className={!open ? "filterOPtion" : " openMenu"}>
      <p style={{ color: "white" }}>
        <span style={{ marginLeft: "15px" }}>
          <IoOptionsSharp
            className={!open ? "filterbtn-1" : " filterbtn-2"}
            fontSize="25px"
            style={{ marginRight: 10, marginTop: 10 }}
            onClick={() => setOpen(!open)}
          />
        </span>
      </p>

      <div className={!open ? "filter-Con-0" : "filter-Con-1"}>
        <Form>
          <div className="filter-ad">
            <Form.Check
              inline
              label="Ascending"
              name="group1"
              type="radio"
              id="ascending"
            />
            <Form.Check
              inline
              label="Descending"
              name="group1"
              type="radio"
              id="Descending"
            />
          </div>
          <hr />
          <div className="status-checkbox">
            <Form.Check
              className="ofs"
              inline
              label="Fast Shipping"
              name="group2"
              type="checkbox"
              id="Descending"
            />

            <Form.Check
              className="ofs"
              inline
              label="Out of Stock item"
              name="group2"
              type="checkbox"
              id="Descending"
            />
          </div>
          <hr />
          <div className="rating-con">
            Rating:
            <Rating
              rating={rating}
              onClick={(i) => setRating(i)}
              style={{ cursor: "pointer" }}
            />
          </div>
          <hr />
          <Button variant="secondary">Clear all filter</Button>
          <div></div>
        </Form>
      </div>
    </div>
  );
};

export default Filter;
